<template>
  <!--<div class="wrap">
      <header>
          </h3>
      </header>
      <div class="content">
        <div class="container-fluid">
            <ul class="mine-tab">
              <li class="active"><router-link to="/customer"></router-link></li>
              <li><router-link to="/house"></router-link></li>
              <li><router-link to="/order"></router-link></li>
              <li><router-link to="/record"></router-link></li>
            </ul>
        </div>-->
        <div class="container cd-content">
            <div class="cd-main">
              <form class="form-horizontal cd-con relative" v-if="customerInfo">
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{customerInfo.customerName}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{vagueMobile}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{customerInfo.homePhone ? customerInfo.homePhone : ''}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{vaguereserveMobile}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{(customerInfo.taskDistribute&&customerInfo.taskDistribute.invitationCode) || ''}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{customerInfo.customerTag || '' | tag-filter}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{customerInfo.taskDistribute.taskLevel}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{(customerInfo.taskDistribute&&customerInfo.taskDistribute.introducer) || ''}}</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">{{(customerInfo.taskDistribute&&customerInfo.taskDistribute.introducerTel)|| ''}}</p>
                  </div>
                <div class="form-inline">
                  <label class="col-xs-4 text-normal"></label>
                  <p class="col-xs-8">{{(customerInfo.taskDistribute&&customerInfo.taskDistribute.promoteSource)|| ''}}</p>
                </div>
                <div class="form-inline">
                  <label class="col-xs-4 text-normal"></label>
                  <p class="col-xs-8">{{(customerInfo.taskDistribute&&customerInfo.taskDistribute.remark)|| ''}}</p>
                </div>
                  <!--<div class="form-inline">
                      <label class="col-xs-4 text-normal"></label>
                      <p class="col-xs-8">2727</p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4"></label>
                      <p class="col-xs-8"></p>
                  </div>
                  <div class="form-inline">
                      <label class="col-xs-4"></label>
                      <p class="col-xs-8">A</p>
                  </div>-->
                  <div class="btn-box">
                      <button :disabled="loading" v-if="customerInfo.taskDistribute.status === '1'" type="button" class="btn btn-success" @click="handleEdit"></button>
                      <button :disabled="loading" v-if="customerInfo.taskDistribute.status === '1'" type="button" class="btn btn-success" @click="handleNew"></button>
                      <button :disabled="loading" v-if="customerInfo.taskDistribute.status === '1'" type="button" class="btn btn-success" @click="handleInvalid"></button>
                      <button :disabled="loading" v-if="customerInfo.taskDistribute.status === '1'" type="button" class="btn btn-success" @click="update=true"></button>
                    </div>
                  <!--<div class="row margin0">-->
                    <!--<div class="col-xs-4">
                        <button type="button" class="btn btn-success  btn-block" @click="invalid"></button>
                    </div>-->
                    <!--<div class="col-xs-6">
                        <button type="button" class="btn btn-success  btn-block" @click="handleEdit"></button>
                    </div>
                     <div class="col-xs-6">
                        <button type="button" class="btn btn-success  btn-block" @click="handleNew"></button>
                    </div>
                  </div>-->
                  <!--<button type="button" class="btn btn-success  btn-block" @click="invalid"></button>-->
                  <!--<button type="button" class="btn btn-success  btn-block">
                    <router-link to="/editCustomer"></router-link>
                  </button>-->
                  <!--<button type="button" class="btn btn-success  btn-block" @click="handleEdit"></button>-->
                  <!--<button type="button" class="btn btn-success  btn-block">
                    <router-link to="/addContants"></router-link>

                  </button>-->
                  <!--<button type="button" class="btn btn-success  btn-block" @click="handleNew"></button>-->
                  <div class="statusMark" v-if="customerInfo.taskDistribute.status !== '1'">
                    
                  </div>
              </form>

              <form class="form-horizontal cd-con relative" v-if="!customerInfo">
                <div class="form-inline">
                      <p class="col-xs-12 text-center"></p>
                </div>
              </form>

          </div>
          <Loading v-show="loading"></Loading>
           <Modal :show.sync="modal" :auto="auto">
            <div class="text-center" style="padding:20px 0">{{msg}}</div>
            <div class="modal-footer" style="padding-bottom:0">
              <button v-show="btnShow" @click="cancel" type="button" class="btn btn-default" data-dismiss="modal"></button>
              <button :disabled="loading" v-show="btnShow" @click="confirm" type="button" class="btn btn-primary bg578f6e"></button>
            </div>
          </Modal>

          <div :class="['tanboxbg',update ? '' : 'hidden']" @click.self="update=false">
            <div class="tanbox">
              <ul>
                <li v-for="item of personList"><label class="lebal-tag"><input name="radio" type="radio" v-model="jobNum" :value="item.jobNum">{{item.empName}}</label></li>
              </ul>
              <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal" @click.self="update=false"></button>
                <button type="button" class="btn btn-primary bg578f6e" @click="handleUpdate"></button>
              </div>
            </div>
          </div>
        </div>

</template>


<script>
import qs from 'qs'
import axios from 'axios'
import Modal from '../Modal.vue'
import Loading from '../Loading.vue'
// import Util from '../../utils/common'
export default {
  name: 'customerDetail',
  data (){
    return {
      loading: false,
      customerInfo: {
        taskDistribute: {status: null, taskLevel: null}
      }, // 
      houseInfo: null, // 
      orderInfo: null, // 
      record: null, // 
      modal: false,   // 
      msg: '',    // 
      btnShow: false,  // 
      auto: true,   // 
      // 
      update: false,
      personList: [],
      // 
      jobNum: ''
    }
  },
  components: {
    Modal,
    Loading
  },
  methods: {
    // 
    handleUpdate(){
      if(this.jobNum === ''){
        return false
      }
      this.loading = true
      let postData = {
        taskNo: this.customerInfo.taskDistribute.taskNo,
        jobNum: this.jobNum,
        oldJobNum: this.customerInfo.taskDistribute.mechandiser,
        isWchat: true
      }
      axios.post('/api/task/update', qs.stringify(postData), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}).then((res) => {
        if(res.data.code === '1'){
          this.update = false
          setTimeout(() => {
            this.$router.go(-1)
          }, 1500)
        }
        this.modal = true
        this.msg = res.data.message
      }).finally(_ => {
        this.loading = false
      })
    },
    // 
    fetchList () {
      this.loading = true
      axios.get('/api/employee/listCollection', {params: {
        store: this.customerInfo.taskDistribute.store,
        jobNum: this.customerInfo.taskDistribute.mechandiser,
        isWchat: true
      }}).then((res) => {
        if(res.data.code === '1'){
          this.personList = res.data.data.rows
        }
      }).finally(_ => {
        this.loading = false
      })
    },
    // 
    fetchDate () {
      var self = this
      this.loading = true
      axios.get('/api/customer/getCustomerByCustNo?customerNo=' + this.query.customerNo + '&taskNo=' + this.query.id).then(function(res){
        if(res.data.code === '1'){
          self.customerInfo = res.data.data
          self.fetchList()
        }
      }).finally(_ => {
        this.loading = false
      })
    },
    // 
    // 
    handleNew () {
      this.$router.push('/addContants?customerNo=' + this.query.customerNo + '&taskNo=' + this.query.id)
    },
     // 
    handleEdit () {
      this.$router.push('/editCustomer?customerNo=' + this.query.customerNo + '&taskNo=' + this.query.id)
    },
    handleInvalid () {
      this.modal = true   // 
      this.msg = ''    // 
      this.btnShow = true  // 
      this.auto = false   // 
    },
    // 
    confirm () {
      this.loading = true
      this.btnShow = false
      axios.post('/api/customer/invalidateuserapply?customerNo=' + this.query.customerNo + '&taskNo=' + this.query.id).then((res) => {
        if(res.data.code === '1'){
          this.customerInfo.taskDistribute.status = '2'
        }
        this.auto = true
        this.msg = res.data.message
      }).finally(_ => {
        this.loading = false
      })
    },
    // 
    cancel () {
      this.modal = false
      this.btnShow = false
      this.auto = true
    }
  },
  created(){
    this.fetchDate()
  },
  computed: {
    query () {
      return this.$route.query
    },
    vagueMobile () {
      return this.customerInfo.customerMobile
      ? this.customerInfo.customerMobile.substring(0, 3) + '****' + this.customerInfo.customerMobile.substring(7, this.customerInfo.customerMobile.length)
      : ''
    },
    vaguereserveMobile () {
      return this.customerInfo.reserveMobile
      ? this.customerInfo.reserveMobile.substring(0, 3) + '****' + this.customerInfo.reserveMobile.substring(7, this.customerInfo.reserveMobile.length)
      : ''
    }
  },
  watch: {

  }
}
</script>



